body[scheme=bright_light], body[scheme=space_gray]  {
    --orange: rgb(235, 152, 0);
    --light-orange: rgb(255, 207, 118);
    --tabbar_active_icon: var(--orange)!important;
    --header_tint: var(--orange) !important;
    --accent: var(--orange) !important;
    --counter_primary_background: var(--orange) !important;
    --button_primary_background: var(--orange) !important;
    --icon_outline_secondary: var(--orange) !important;
    --overlay_status_icon: var(--orange) !important;
    --button_tertiary_foreground: var(--orange) !important;
}

.shadowPanelHeader > .vkuiPanelHeader__fixed {
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
}

.messageList {
    min-height: 100%;
    width: 100%;
    padding-top: 10px;
}

.chatPanel > .vkuiPanel__in {
    justify-content: end;
}

.messageBox {
    min-height: 30px;
    margin-bottom: 10px;
    margin-left: 10px;
    margin-right: 10px;
    display: flex;
    column-gap: 5px;
    align-items: flex-end;
}

.messageBox.self {
    justify-content: end;
}

.messageTextBox {
    color: white;
    background-color: var(--light-orange);
    padding: 5px 10px;
    border-radius: 12px;
    max-width: 70%;
}

.messageTextBox.self {
    background-color: var(--orange);
}

.messageCaption {
    padding-top: 2px;
    font-size: x-small;
    text-align: end;
}

.messageTitle {
    font-size: smaller;
    font-weight: 600;
    color: rgb(244, 122, 0);
}

.profileBIO {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px;
}

.profileTitle {
    margin-top: 20px;
}

.profileSubTitle {
    color: var(--vkui--color_text_secondary);
    margin-top: 5px;
    margin-bottom: 20px;
}

.profileFriends {
    column-gap: 10px;
}

.textSeparator {
    position: relative;
    width: 100%;
    text-align: center;
    margin-bottom: 10px;
    color: gray;
    display: flex;
    justify-content: center;
    align-items: center;
}

.textSeparator > .line {
    position: absolute;
    top: 55%;
    height: 1px;
    background-color: gray;
    width: 80%;
    z-index: -1;
}

.textSeparator > .text {
    background-color: #fff;
    padding: 0px 5px;
    font-size: 0.9em;
}

.d-f-center > .vkuiGroup__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 5px;
}